import { useState, useEffect } from 'react';
// import { useNavigate } from 'react-router-dom';
import toast from 'react-hot-toast';
// import { usePlacesWidget } from 'react-google-autocomplete';
import * as Yup from 'yup';
import { Formik } from 'formik';
import PropTypes from 'prop-types';
import {
  // Autocomplete,
  Box,
  Button,
  Card,
  CardContent,
  // CardHeader,
  // Checkbox,
  // FormControlLabel,
  FormHelperText,
  Grid,
  TextField,
  // Typography
} from '@material-ui/core';
// import FileDropzone from '../../../FileDropzone';
// import QuillEditor from '../../QuillEditor';
/* eslint-disable import/no-duplicates */
// import firebase from '../../../lib/firebase';
// import Map from '../map/Map';
// import { functions } from '../../../../lib/firebase';
import ClientRepository from '../../../../repositories/client.repository';

const OrderForm = (props) => {
  const clientRepository = new ClientRepository();
  // const navigate = useNavigate();
  // const country = 'us';
  // const clientsOptions = [{ value: '', name: '' }];
  const [clientsOptions, setclientsOptions] = useState([]);
  const [isSubmit, setIsSubmit] = useState(false);
  // const { ref, options } = usePlacesWidget({
  //   apiKey: 'AIzaSyB6ljOYFwZNV1Dm4NXgmbqOdEL0GbOGVnQ',
  //   onPlaceSelected: (place) => {
  //     console.log(place);
  //   }
  // });
  const { setStatusForm, setDataClient, dataClient, setStatusRegisterClient } = props;

  // const { ref: materialRef } = usePlacesWidget({
  //   apiKey: 'AIzaSyB6ljOYFwZNV1Dm4NXgmbqOdEL0GbOGVnQ',
  //   onPlaceSelected: (place) => console.log(place),
  //   inputAutocompleteValue: 'country',
  //   options: {
  //     componentRestrictions: { country },
  //   },
  // });

  useEffect(() => {
    // console.log(brandsOptions);
    clientRepository.getAllClients().then((res) => {
      setclientsOptions(res);
      console.log(res);
    });
  }, []);

  return (
    <Formik
      initialValues={{
        client_dni: '',
        // submit: null
      }}
      validationSchema={Yup
        .object()
        .shape({
          client_dni: Yup.string().max(255).required('El DNI del cliente es obligatorio'),
        })}
      onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
        try {
          const client = clientsOptions.filter((data) => data.dni === values.client_dni);
          if (client.length === 1) {
            setDataClient(client[0]);
            toast.success('El Cliente existe!');
            setStatusRegisterClient(true);
          } else {
            toast('El Cliente no esta Registrado!', {
              icon: '⚠',
            });
            setDataClient({ dni: values.client_dni });
            // toast.name('El Cliente no existe!');
          }
          setStatusForm('2');
          setSubmitting(true);
          setIsSubmit(true);
          console.log(dataClient);
          return true;
        } catch (err) {
          console.error(err);
          toast.error('Ha Ocurrido un Error');
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
          setIsSubmit(false);
          return true;
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        // isSubmitting,
        // setFieldValue,
        touched,
        values
      }) => (
        <form
          onSubmit={handleSubmit}
          {...props}
        >
          <Grid
            container
            spacing={3}
          >
            <Grid
              item
              lg={12}
              md={12}
              xs={12}
            >
              <Card>
                <CardContent>
                  <Grid
                    container
                    spacing={3}
                  >
                    <Grid
                      item
                      md={12}
                      xs={12}
                    >
                      <TextField
                        error={Boolean(touched.client_dni && errors.client_dni)}
                        fullWidth
                        helperText={touched.client_dni && errors.client_dni ? 'Campo Requerido' : null}
                        label="DNI del Cliente"
                        name="client_dni"
                        type="text"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.client_dni}
                        variant="outlined"
                      />
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
            </Grid>
            <Grid
              item
              lg={2}
              md={2}
              xs={12}
            >
              {errors.submit && (
                <Box sx={{ mt: 3 }}>
                  <FormHelperText error>
                    {errors.submit}
                  </FormHelperText>
                </Box>
              )}
              <Box
                sx={{
                  display: 'flex',
                  justifyContent: 'flex-end',
                  mt: 3
                }}
              >
                <Button
                  color="primary"
                  disabled={isSubmit}
                  type="submit"
                  variant="contained"
                  onClick={() => {
                    // setStatusForm('2');
                  }}
                >
                  Continuar
                </Button>
              </Box>
            </Grid>
          </Grid>
        </form>
      )}
    </Formik>
  );
};

OrderForm.propTypes = {
  setStatusForm: PropTypes.func,
  setDataClient: PropTypes.func,
  dataClient: PropTypes.string,
  setStatusRegisterClient: PropTypes.func
};

export default OrderForm;
