import { useEffect, useState } from 'react';
import { Link as RouterLink, useNavigate } from 'react-router-dom';
import { Helmet } from 'react-helmet-async';
import { Box, Breadcrumbs, Button, Container, Grid, Link, Typography } from '@material-ui/core';
import useSettings from '../../../../hooks/useSettings';
import ArrowLeftIcon from '../../../../icons/ArrowLeft';
import ChevronRightIcon from '../../../../icons/ChevronRight';
import gtm from '../../../../lib/gtm';
import OrderForm from '../../../../components/dashboard/store/orders/OrderForm';
import OrderForm1 from '../../../../components/dashboard/store/orders/OrderForm1';
import Modal9 from '../../../../components/widgets/modals/Modal9';

const OrderCreate = () => {
  const { settings } = useSettings();
  const [statusForm, setstatusform] = useState('1');
  const [dataClient, setdataclient] = useState({});
  const [statusRegisterClient, setstatusregisterclient] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    gtm.push({ event: 'page_view' });
  }, []);

  return (
    <>
      <Helmet>
        <title>Dashboard: Crear Orden | Mac </title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: 'background.default',
          minHeight: '100%',
          py: 8
        }}
      >
        <Container maxWidth={settings.compact ? 'xl' : false}>
          <Grid
            container
            justifyContent="space-between"
            spacing={3}
          >
            <Grid item>
              <Typography
                color="textPrimary"
                variant="h5"
              >
                Crear Orden
              </Typography>
              <Breadcrumbs
                aria-label="breadcrumb"
                separator={<ChevronRightIcon fontSize="small" />}
                sx={{ mt: 1 }}
              >
                <Link
                  color="textPrimary"
                  component={RouterLink}
                  to="/dashboard"
                  variant="subtitle2"
                >
                  Dashboard
                </Link>
                <Typography
                  color="textSecondary"
                  variant="subtitle2"
                >
                  Crear Orden
                </Typography>
              </Breadcrumbs>
            </Grid>
            <Grid item>
              <Box sx={{ m: -1 }}>
                <Button
                  color="primary"
                  // component={RouterLink}
                  startIcon={<ArrowLeftIcon fontSize="small" />}
                  sx={{ mt: 1 }}
                  variant="outlined"
                  onClick={() => {
                    if (statusForm === '1' || statusForm === '3') {
                      navigate('/dashboard');
                    } else if (statusForm === '2') {
                      setstatusform('1');
                    }
                  }}
                >
                  {statusForm === '2' ? 'Regresar' : 'Cancelar'}
                </Button>
              </Box>
            </Grid>
          </Grid>
          <Box sx={{ mt: 3 }}>
            { statusForm === '1' && (
            <OrderForm
              setStatusForm={setstatusform}
              setDataClient={setdataclient}
              dataClient={dataClient}
              setStatusRegisterClient={setstatusregisterclient}
            />
            )}
            { statusForm === '2' && (
            <OrderForm1
              setStatusForm={setstatusform}
              dataClient={dataClient}
              statusRegisterClient={statusRegisterClient}
            />
            )}
            { statusForm === '3'
            && (
            <Grid
              container
              spacing={3}
            >
              <Grid
                item
                lg={12}
                md={12}
                xs={12}
              >
                <Modal9 />
              </Grid>
            </Grid>
            )}
          </Box>
        </Container>
      </Box>
    </>
  );
};

export default OrderCreate;
