import { useState } from 'react';

const useInput = (initialValue) => {
  const [value, setValue] = useState(initialValue);
  const [suggestions, setSuggestions] = useState([]);

  const handleChange = async (event) => {
    setValue(event.target.value);
    try {
      const endpoint = `https://api.mapbox.com/geocoding/v5/mapbox.places/${event.target.value}.json?access_token=pk.eyJ1IjoiZXplcXVpZWxmcmlhczIwIiwiYSI6ImNsMmpkdWlxcTAxM2QzY2t3ajR0N3pyOGUifQ.gxfc3ag0w310q1l9t0VYJw&autocomplete=true`;
      const response = await fetch(endpoint);
      const results = await response.json();
      setSuggestions(results?.features);
      console.log(results);
    } catch (error) {
      console.log(error);
    }
  };
  return {
    value,
    onchange: handleChange,
    setValue,
    suggestions,
    setSuggestions
  };
};

export default useInput;
