import { useEffect } from 'react';
// import { useNavigate } from 'react-router-dom';
import toast from 'react-hot-toast';
import * as Yup from 'yup';
import { Formik } from 'formik';
import PropTypes from 'prop-types';
import numeral from 'numeral';
import {
  // Autocomplete,
  Box,
  Button,
  Card,
  CardContent,
  Divider,
  Autocomplete,
  // Checkbox,
  // FormControlLabel,
  CardHeader,
  FormHelperText,
  Grid,
  TextField,
  Typography
} from '@material-ui/core';
import ClientRepository from '../../../../repositories/client.repository';
import OrderRepository from '../../../../repositories/order.repository';
import MobileDateTimePicker from '@material-ui/lab/MobileDateTimePicker';
import useInput from '../../../../hooks/useInput';
// import Map from '../../map/Map';
// import FileDropzone from '../../FileDropzone';
// import QuillEditor from '../../QuillEditor';
// import firebase from '../../../../lib/firebase';
// import useAuth from '../../../hooks/useAuth';
// import NumberFormat from 'react-number-format';

const OrderForm1 = (props) => {
  const clientRepository = new ClientRepository();
  const orderRepository = new OrderRepository();
  const address = useInput('');
  console.log(address.suggestions);
  // const auth = useAuth();
  // const navigate = useNavigate();
  // const [files, setFiles] = useState([]);
  // const clientsOptions = [];
  // const vehiclesOptions = [];
  // const empty = '';
  const { setStatusForm, dataClient, statusRegisterClient } = props;
  // console.log(dataClient);
  const registerClient = async (values) => {
    let response;
    const dataClientRegister = {
      dni: values.client_dni,
      name: values.client_name,
      lastname: values.client_lastname,
      phones: [values.phones],
      direction: values.direction,
      zipcode: values.zipcode,
      status_id: '0',
      coordinates: '-87, 64',
      score: '0'
    };
    await clientRepository.createClient(dataClientRegister).then((res) => {
      response = res;
    }).catch((err) => {
      console.log('Error al crear usuario', err);
      // return response;
    });
    return response;
  };

  const registerOrden = async (values) => {
    let response;
    if (statusRegisterClient) {
      const dataOrdenCreate = {
        client_id: dataClient.id,
        store_id: 3,
        status_id: '0',
        price: 20,
        final_price: 20,
        preparation_estimated_time: values.preparation_estimated_time,
        delivery_estimated_time: values.delivery_estimated_time
      };
      response = await orderRepository.createOrder(dataOrdenCreate);
    } else {
      response = await registerClient(values).then(async (res) => {
        const dataOrdenCreate = {
          client_id: res.data.data.id,
          store_id: 3,
          status_id: '0',
          price: 20,
          final_price: 20,
          preparation_estimated_time: values.preparation_estimated_time,
          delivery_estimated_time: values.delivery_estimated_time
        };
        return orderRepository.createOrder(dataOrdenCreate);
      });
    }
    return response;
  };

  useEffect(() => {
  }, []);

  return (
    <Formik
      initialValues={{
        client_dni: dataClient.dni,
        client_name: statusRegisterClient ? dataClient.name : '',
        client_lastname: statusRegisterClient ? dataClient.lastname : '',
        phones: statusRegisterClient ? dataClient.phones[0].toString() : '',
        direction: statusRegisterClient ? dataClient.direction : '',
        zipcode: statusRegisterClient ? dataClient.zipcode : '',
        origin_location: '',
        destination_location: '',
        price: '',
        final_price: '',
        preparation_estimated_time: new Date().toISOString(),
        delivery_estimated_time: new Date().toISOString()
        // submit: null
      }}
      validationSchema={Yup
        .object()
        .shape({
          client_dni: Yup.string().max(255).required(),
          client_name: Yup.string().max(255).required(),
          client_lastname: Yup.string().max(255).required(),
          phones: Yup.string().max(255).required(),
          direction: Yup.string().max(255).required('Campo Requerido'),
          zipcode: Yup.string().max(255).required(),
          price: Yup.string().max(255),
          final_price: Yup.string().max(255),
          preparation_estimated_time: Yup.date().required('Campo Requerido'),
          delivery_estimated_time: Yup.date().required('Campo Requerido'),
        })}
      onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
        try {
          console.log(values);
          registerOrden(values).then((res) => {
            console.log(res);
            if (res.status === 201 || res.status === '201') {
              setStatusForm('3');
            }
          });
        } catch (err) {
          console.error(err);
          toast.error('Ha Ocurrido un error para crear su Orden!');
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        setFieldValue,
        touched,
        values
      }) => (
        <form
          onSubmit={handleSubmit}
          {...props}
        >
          <Grid
            container
            spacing={3}
          >
            <Grid
              item
              lg={12}
              md={12}
              xs={12}
            >
              <Card>
                <CardHeader title="Datos del Cliente" />
                <Divider />
                <CardContent>
                  <Grid
                    container
                    spacing={3}
                  >
                    <Grid
                      item
                      md={6}
                      xs={12}
                    >
                      <TextField
                        error={Boolean(touched.client_name && errors.client_name)}
                        fullWidth
                        disabled={statusRegisterClient}
                        helperText={touched.client_name && errors.client_name ? 'Campo Requerido' : null}
                        label="Nombre"
                        name="client_name"
                        type="text"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.client_name}
                        variant="outlined"
                      />
                    </Grid>
                    <Grid
                      item
                      md={6}
                      xs={12}
                    >
                      <TextField
                        error={Boolean(touched.client_lastname && errors.client_lastname)}
                        fullWidth
                        disabled={statusRegisterClient}
                        helperText={touched.client_lastname && errors.client_lastname ? 'Campo Requerido' : null}
                        label="Apellido"
                        name="client_lastname"
                        type="text"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.client_lastname}
                        variant="outlined"
                      />
                    </Grid>
                    <Grid
                      item
                      md={6}
                      xs={12}
                    >
                      <TextField
                        error={Boolean(touched.client_dni && errors.client_dni)}
                        fullWidth
                        disabled
                        helperText={touched.client_dni && errors.client_dni ? 'Campo Requerido' : null}
                        label="DNI"
                        name="dni"
                        type="text"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.client_dni}
                        // variant="outlined"
                      />
                    </Grid>
                    <Grid
                      item
                      md={6}
                      xs={12}
                    >
                      <TextField
                        error={Boolean(touched.phones && errors.phones)}
                        fullWidth
                        disabled={statusRegisterClient}
                        helperText={touched.phones && errors.phones ? 'Campo Requerido' : null}
                        label="Telefono"
                        name="phones"
                        type="number"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.phones}
                        variant="outlined"
                      />
                    </Grid>
                    <Grid
                      item
                      md={6}
                      xs={6}
                    >
                      <TextField
                        error={Boolean(touched.zipcode && errors.zipcode)}
                        fullWidth
                        disabled={statusRegisterClient}
                        helperText={touched.zipcode && errors.zipcode ? 'Campo Requerido' : null}
                        label="Código Postal"
                        name="zipcode"
                        type="text"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.zipcode}
                        variant="outlined"
                      />
                    </Grid>
                    <Grid
                      item
                      md={12}
                      xs={12}
                    >
                      <TextField
                        error={Boolean(touched.direction && errors.direction)}
                        fullWidth
                        disabled={statusRegisterClient}
                        helperText={touched.direction && errors.direction}
                        label="Dirección"
                        name="direction"
                        type="text"
                        multiline
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.direction}
                        variant="outlined"
                        rows={6}
                      />
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
            </Grid>
            <Grid
              item
              lg={12}
              md={12}
              xs={12}
            >
              <Box sx={{ mt: 3 }}>
                <Card>
                  <CardHeader title="Descripción de la Orden" />
                  <Divider />
                  <CardContent>
                    <Autocomplete
                      getOptionLabel={(option) => option.place_name}
                      isOptionEqualToValue={(option, value) => option.place_name === value.place_name}
                      // getOptionLabel={(option) => option.name}
                      onChange={(e, value) => {
                        setFieldValue('origin_location', value);
                      }}
                      options={address.suggestions}
                      renderInput={(params) => (
                        <TextField
                          error={Boolean(touched.origin_location && errors.origin_location)}
                          fullWidth
                          label="Ubicación de Origen"
                          placeholder="Introduzca la ubicación de origen"
                          helperText={touched.origin_location && errors.origin_location}
                          name="origin_location"
                          variant="outlined"
                          onChange={(e) => {
                            address.onchange(e);
                          }}
                          // inputRef={ref}
                          value={values.origin_location}
                          {...params}
                        />
                      )}
                    />
                    <Autocomplete
                      getOptionLabel={(option) => option.place_name}
                      isOptionEqualToValue={(option, value) => option.place_name === value.place_name}
                      // getOptionLabel={(option) => option.name}
                      onChange={(e, value) => {
                        setFieldValue('destination_location', value);
                      }}
                      options={address.suggestions}
                      sx={{ mt: 3 }}
                      renderInput={(params) => (
                        <TextField
                          error={Boolean(touched.destination_location && errors.destination_location)}
                          fullWidth
                          label="Ubicación de Destino"
                          placeholder="Introduzca la ubicación de "
                          helperText={touched.destination_location && errors.destination_location}
                          name="destination_location"
                          variant="outlined"
                          onChange={(e) => {
                            address.onchange(e);
                          }}
                          // inputRef={ref}
                          value={values.destination_location}
                          {...params}
                        />
                      )}
                    />
                    <Box sx={{ mt: 2 }}>
                      <MobileDateTimePicker
                        label="Tiempo estimada de Preparación"
                        onChange={(date) => setFieldValue('preparation_estimated_time', date.toISOString())}
                        renderInput={(inputProps) => (
                          <TextField
                            fullWidth
                            variant="outlined"
                            {...inputProps}
                          />
                        )}
                        value={values.preparation_estimated_time}
                      />
                    </Box>
                    <Box sx={{ mt: 2 }}>
                      <MobileDateTimePicker
                        label="Tiempo estimado para el Delivery"
                        onChange={(date) => setFieldValue('delivery_estimated_time', date.toISOString())}
                        renderInput={(inputProps) => (
                          <TextField
                            fullWidth
                            variant="outlined"
                            {...inputProps}
                          />
                        )}
                        value={values.delivery_estimated_time}
                      />
                    </Box>
                  </CardContent>
                </Card>
              </Box>
            </Grid>
            <Grid
              item
              lg={12}
              md={12}
              xs={12}
            >
              <Box sx={{ mt: 3 }}>
                <Card>
                  <CardHeader title="Tarifa" />
                  <Divider />
                  <CardContent>
                    <Box
                      sx={{
                        display: 'flex',
                        justifyContent: 'space-between',
                        mt: 2
                      }}
                    >
                      <Typography
                        color="textPrimary"
                        variant="subtitle2"
                      >
                        Subtotal
                      </Typography>
                      <Typography
                        color="textPrimary"
                        variant="subtitle2"
                      >
                        $
                        {numeral(20).format('00.00')}
                      </Typography>
                    </Box>
                    {/* <Box
                      sx={{
                        display: 'flex',
                        justifyContent: 'space-between',
                        mt: 2
                      }}
                    >
                      <Typography
                        color="textPrimary"
                        variant="subtitle2"
                      >
                        Shipping Tax
                      </Typography>
                      <Typography
                        color="textPrimary"
                        variant="subtitle2"
                      >
                        $
                        {numeral(10).format('00.00')}
                      </Typography>
                    </Box> */}
                    <Divider sx={{ my: 2 }} />
                    <Box
                      sx={{
                        display: 'flex',
                        justifyContent: 'space-between'
                      }}
                    >
                      <Typography
                        color="textPrimary"
                        variant="subtitle2"
                      >
                        Total
                      </Typography>
                      <Typography
                        color="textPrimary"
                        variant="subtitle2"
                      >
                        $
                        {numeral(20).format('00.00')}
                      </Typography>
                    </Box>
                  </CardContent>
                </Card>
              </Box>
            </Grid>
            {/* <Grid
              item
              lg={12}
              md={12}
              xs={12}
            >
              <Box sx={{ mt: 3 }}>
                <Card>
                  <CardHeader title="Dirección" />
                  <CardContent>
                    <Box
                      sx={{
                        display: 'flex',
                        justifyContent: 'flex-end',
                        padding: 3
                      }}
                    >
                      <Map />
                    </Box>
                  </CardContent>
                </Card>
              </Box>
            </Grid> */}
            <Grid
              item
              lg={2}
              md={2}
              xs={12}
            >
              {errors.submit && (
                <Box sx={{ mt: 3 }}>
                  <FormHelperText error>
                    {errors.submit}
                  </FormHelperText>
                </Box>
              )}
              <Box
                sx={{
                  display: 'flex',
                  justifyContent: 'flex-end',
                  mt: 3
                }}
              >
                <Button
                  color="primary"
                  disabled={isSubmitting}
                  type="submit"
                  variant="contained"
                  onClick={() => setStatusForm('2')}
                >
                  Registrar Orden
                </Button>
              </Box>
            </Grid>
          </Grid>
        </form>
      )}
    </Formik>
  );
};
OrderForm1.propTypes = {
  setStatusForm: PropTypes.func,
  dataClient: PropTypes.object,
  statusRegisterClient: PropTypes.bool
};

export default OrderForm1;
